import Link from "next/link";
import { FaUserCircle, FaChevronDown } from "react-icons/fa";
import "../../styles/navbar.css";  // for Navbar.tsx


const services = [
  "Personal Loan",
  "Business Loan",
  "Education Loan",
  "Home Loan",
  "Loan Against Property",
  "Loan Against Insurance Policies",
  "Loan Against Mutual Funds & Shares",
  "Health Insurance",
  "Life Insurance",
  "Credit Cards"
];

export default function Navbar() {
  return (
    <nav className="navbar">
      <div className="navbar-container">
        {/* Logo */}
        <div className="logo">
          <img src="assets/logo.jpg" alt="SivantoCapital Logo" />
        </div>

        {/* Menu */}
        <ul className="menu">
          <li><Link href="/">Home</Link></li>
          <li><Link href="/about">About</Link></li>

          {/* Services Dropdown */}
          <li className="dropdown">
            <span className="dropdown-trigger">
              <strong>
                Services <FaChevronDown className="dropdown-arrow" />
              </strong>
            </span>
            <ul className="dropdown-menu">
              {services.map(service => (
                <li key={service}>
                  <Link href={`/service/${encodeURIComponent(service)}`}>
                    {service}
                  </Link>
                </li>
              ))}
            </ul>
          </li>

          <li><Link href="/loan-process">Loan Process</Link></li>
          <li><Link href="/contact">Contact</Link></li>

          {/* Buttons */}
          <li>
            <button className="apply-btn">
              <span className="apply-line1">Apply</span>
              <span className="apply-line2">Button</span>
            </button>
          </li>
          <li>
            <button className="admin-btn" aria-label="Admin">
              <FaUserCircle />
            </button>
          </li>
        </ul>
      </div>
    </nav>
  );
}
